#cardContainer{
    display: grid;
    grid-template-rows: repeat(3,30px) 1fr;
    grid-template-columns: 500px 1fr;
    margin: 20px 20px 0 0;
  }
  
  #printed_name{
    grid-row: 1 / 2;
    grid-column: 1 / 2;
  }
  
  #name{
    grid-row: 2 / 3;
    grid-column: 1 / 2;
  }
  
  #color_identity{
    grid-row: 3 / 4;
    grid-column: 1 / 2;
  }
  
  #printed_text{
    grid-row: 4 / 5;
    grid-column: 1 / 2;
    white-space: pre-wrap;
  }
  
  #image{
    grid-row: 1 / 5;
    grid-column: 2 / 3;
    margin: 0 0 20px 20px;
  }